#header {
    background-color: #3b2b1a;
    color: #f5e1c8;
    padding: 10px;
    text-align: center;
    font-size: 1.2rem;
}

#top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #d2a679;
}

#logo-container {
    display: flex;
    align-items: center;
    gap: 10px;
}

#store-logo {
    width: 120px;
    height: auto;
}

#store-name {
    font-size: 2rem;
    font-weight: bold;
}

#search-bar {
    display: flex;
    background-color: #8b5e3b;
    padding: 5px 10px;
    border-radius: 5px;
}

#search-bar input {
    border: none;
    outline: none;
    background: transparent;
    color: #f5e1c8;
}

#icons {
    display: flex;
    gap: 15px;
}

.icon {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #5a3e2b;
}

.icon img {
    width: 30px;
    height: 30px;
}

#main-nav ul {
    list-style: none;
    display: flex;
    gap: 20px;
    justify-content: center;
    padding: 0;
}

#main-nav a {
    text-decoration: none;
    color: #5a3e2b;
    font-weight: bold;
}

.carousel-img {
    display: flex;
    justify-content: center;
}

.section-title {
    background-color: #3b2b1a;
    color: #f5e1c8;
    padding: 10px;
    border-radius: 8px;
    text-align: center;
}

.flex-container {
    display: flex;
    gap: 20px;
    justify-content: center;
}

#footer {
    background-color: #3b2b1a;
    color: #f5e1c8;
    text-align: center;
    padding: 10px;
}

.columns {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin: 20px 0;
}

.tree {
    text-align: center;
}
